// react
import React from "react";
// zerod
// import { ZsearchListHOC } from "zerod";
import {Tag } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
// import pageHeader from "@/views/Main/component-doc/ZpageHeader-doc/pageHeader";
// import api from "@/App.api.js";

import getDemo from "./demos";

const showDemo = function(Component) {
	return <Component />;
};
import doc from "zerod/components/ZsearchListHOC/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		const MyScript = AshowDemoHOC(showDemo(getDemo()), true);
		return <MyScript />;
	},
	demo2: function() {
		const MyScript = AshowDemoHOC(
			showDemo(
				getDemo({
					list: {
						listType: "card",
						paginationType: "infinite",
						getPageSize: (listType, isListCard) => 7,
					},
				}),
			),
			true,
		);
		return <MyScript />;
	},
	demo3: function() {
		const MyScript = AshowDemoHOC(
			showDemo(
				getDemo({
					list: {
						listType: "simple",
						paginationType: "infinite",
						cardSpan:{xxl:8,xl:12,lg:24},
						getPageSize: (listType, isListCard) => 3,
					},
				}),
			),
			true,
		);
		return <MyScript />;
	},
});
