import React from "react";
import { Input, message,Tag } from "antd";
import { ZsearchListHOC, zTool, Zcascader } from "zerod";

import api from "@/App.api.js";

import getEditPage from "./getEditPage.js";
import getDetailPage from "./getDetailPage.js";
import area from "@/mock/area";
import pageHeader from "@/views/Main/component-doc/ZpageHeader-doc/pageHeader";
// let listExports = null;
function getDemo(pageConfig) {
	const config = {
		insertLocation: "mainModal",
		pageHeader: {
			show: true,
			...pageHeader,
		},
		searchForm: {
			defaultExpanded:true,
			labelLayout:"horizontal",
			// array>[object] | null，如果是null则不显示搜索表单
			items: [
				{
					key: "serviceCode",
					label: "服务编码",
					render: (form,changeItems,tool) => {
						console.log("items_render_tool",form,changeItems,tool)
						return <Input placeholder="请输入内容" />;
					},
					options:(tool)=>{
						console.log("form",tool)
					}
				},
				{
					key: "serviceCode2",
					label: "服务编码",
					render: (form) => {
						return <Input placeholder="请输入内容" />;
					},
				},
				{
					key: "serviceCode3",
					label: "服务编码",
					render: (form) => {
						return <Input placeholder="请输入内容" />;
					},
				},
				{
					key: "serviceName",
					label: "服务名称",
					render: (form) => {
						return <Input placeholder="请输入内容" />;
					},
				},
			],
			afterItemsRendered:(form,methods,tool)=>{
				console.log("afterItemsRendered",form,methods,tool)
			}
		},
		list: {
			// table | card
            listType: "table",
			cardCoverRender: null, // listType=="card"时的一个前置render
			panelHeader: "列表",
			// 表格操作列的字段key
			actionDataIndex: "serviceName",
			// antd  Table的参数
			tableParams: {
				expandedRowRender: (record, index, indent, expanded) => {
					return (
						<div>
							<dl className="z-info">
								<dt className="z-info-left z-padding-bottom-10">服务名称</dt>
								<dd className="z-info-right z-padding-bottom-10">{record.serviceName}</dd>
							</dl>
							<dl className="z-info">
								<dt className="z-info-left z-padding-bottom-10">服务编码</dt>
								<dd className="z-info-right z-padding-bottom-10">{record.serviceCode}</dd>
							</dl>
							<dl className="z-info">
								<dt className="z-info-left z-padding-bottom-10">服务描述</dt>
								<dd className="z-info-right z-padding-bottom-10">{record.remark}</dd>
							</dl>
						</div>
					);
				},
			},
			// 表格列map数据数据，同antd的表格 columns
			tableColumns: [
				{
					title: "服务名称",
					dataIndex: "serviceName",
					sorter:true,
					defaultSortOrder:"ascend",
					
				},
				{
					title: "服务编码",
					dataIndex: "serviceCode",
					sorter:true,
					render:(text, record, index, instance)=>{
						return <Tag color="volcano">{text}</Tag>
					}
				},
				{
					title: "约定端口号",
					dataIndex: "servicePort",
					render: (text, record, index, instance) => {
						return <Tag color="magenta">{text}</Tag>;
					},
				},
				{
					title: "服务描述",
					dataIndex: "remark",
					show:false,
				},
			],
			// 是否显示新建按钮
			showAddBtn: true,
			// 新建按钮权限控制代码
			addBtnPermCode: "",
			addPageRender: (panel) => {
				const AddPage = getEditPage({
					pageType: "add",
					headerTitle: "新增服务配置",
					headerContent: "新增一个服务,需要录入服务编码服务名称端口号等信息",
					// 保存数据成功的回调
					afterSubmitSuccess: (closeRightModal) => {
						// 保存数据成功后刷新列表数据
						panel.methods.getListData();
						// 关闭右边modal
						closeRightModal();
					},
				});
				return <AddPage />;
			},
			// 是否显示详情按钮
			showDetailBtn: true,
			// 详情按钮权限控制代码
			detailBtnPermCode: "",
			detailPageRender: (record) => {
				const DetailPage = getDetailPage({ headerTitle: record.serviceName, headerContent: record.remark });
				return <DetailPage detailId={record.id} />;
			},
			// 是否显示修改按钮
			showUpdateBtn: true,
			// 修改按钮权限控制代码
			updateBtnPermCod: "",
			updatePageRender: (record, panel) => {
				const UpdatePage = getEditPage({
					pageType: "update",
					headerTitle: record.serviceName,
					headerContent: record.remark,
					afterSubmitSuccess: (closeRightModal) => {
						panel.methods.getListData(); // 保存数据成功后刷新列表数据
					},
				});
				return <UpdatePage detailId={record.id} />;
			},
			// 是否显示删除按钮
			showDeleteBtn: true,
			// 删除按钮权限控制代码
			deleteBtnPermCod: "",
			// moreBtnType:"menu",
			//更多操作按钮的map数据
			moreBtnMap: [
				{
					key: "0",
					name: "0默认的按钮0",
				},
				{
					key: "1",
					name: "1默认的按钮1",
				},
				{
					key: "2",
					name: "2默认的按钮2",
				},
				{
					key: "3",
					name: "3默认的按钮3",
				},
				{
					key: "4",
					name: "4默认的按钮4",
				},
				{
					key: "5",
					name: "5默认的按钮5",
				},
				{
					key: "6",
					name: "6默认的按钮6",
				},
				{
					key: "7",
					name: "7默认的按钮7",
				},
			],
			//更多操作按钮的的点击事件
			onMoreBtnClick: (item, record) => {
				message.success(`您当前点击的是[${record.serviceName}]这条数据`);
			},
			// 获取列表数据的后台接口函数，其必须内部返回Promise
			listApiInterface: (query,sorter,tool) => {
				console.log(query,sorter,tool)
				return api.config.getServiceList(Object.assign(query, { servcieName: query.serviceName })); //处理字段名
			},
			// 删除按钮的后台接口函数，其必须内部返回Promise
			deleteApiInterface: (data) => {
				return api.config.deleteService({ id: data.id });
			},
			exportSomething: (obj) => {console.log("exportSomething")},
			//是否显示分页
			showPagination: true,
			// 分页类型 paging | infinite
			paginationType: "paging",
			panelBeforeRender: (obj) => {
				return <AreaDemo />;
			},
			panelAfterRender: (obj) => {
				return <div className="z-panel z-margin-top-20 z-text-center"><div className="z-panel-body">panelAfterRender 函数渲染的内容</div></div>;
			},
			moreContentRender: (obj) => {
				return <div className="z-panel is-panel-border z-margin-top-20 z-text-center"><div className="z-panel-body">moreContentRender 函数渲染的内容</div></div>;
			},
		},
	};
	return ZsearchListHOC(zTool.mergeConfig(config, pageConfig ? pageConfig : {}));
}
class AreaDemo extends React.PureComponent {
	methods = {
		onSelect: (items) => {
			let names = items.map((item) => {
				return item.name;
			});
			message.success(`您选择了${names.join('-')}`);
		},
	};
	selections = ["1710121748130980000000166"];
	render() {
		return (
			<div className="z-panel z-margin-bottom-15">
				<div className="z-panel-body">
					<Zcascader
						tree={area.data}
						itemKeys={{ name: "name", id: "areaId", disabled: "disable", children: "children" }}
						selections={this.selections}
						onSelect={this.methods.onSelect}
					/>
				</div>
			</div>
		);
	}
}
export default getDemo;
